import React from 'react';
import { useRender } from 'react-zdog';
import ShapeExtrusion from './ShapeExtrusion';

const spacing = (i, v) => ({ x: 0, y: Math.sin(v * Math.PI * 2 + Date.now() / 2000) * 20, z: i * 2 });
const sideColor = (v) => `rgb(${v * 255}, ${0}, ${0})`;

const AnimatedShapeExtrusion = () => {
    const ref = React.createRef(undefined);
    useRender(() => {
        ref.translate.x = Math.sin(Date.now() / 2000);
    });

    return (
        <ShapeExtrusion spacing={spacing} color={color}/>
    )
}

export default AnimatedShapeExtrusion;